:root {
  /* COLORS */
  --red_1: #BF0902;
  --black_1: #120600;
  --black_2: #000000;
  --gray_1: #757575;
  --gray_2: #666666;
  --gray_3: #DEDEDE;
  --gray_4: #D9D9D9;
  --gray_5: #454545;
  --gray_6: #9A9A9A;
  --gray_7: #535353;
  --gray_8: #303030;
  --white_1: #FFFFFF;
  --white_2: #F5F5F5;
  --white_3: #ADADAD;
  --white_4: #FBFBFB;
  /* FONTS-SIZES */
}

/* FONTS */
/* Inter */
*,
*:before,
*:after {
  box-sizing: border-box;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select,
  textarea,
  input {
    font-size: 1.6rem !important;
  }
}
img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

input[type=submit],
button {
  cursor: pointer;
}

hr {
  display: block;
  height: 0.1em;
  border: 0;
  border-top: 0.1em solid #ccc;
  margin: 1em 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Inter Regular", "Arial", "Tahoma", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-rendering: optimizelegibility;
  overflow-x: hidden;
}

header,
section,
footer,
main {
  width: 100%;
}

main {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

.wrapper {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
