  .no-scroll {
    padding-right: 1.7rem;
  }
}

.overlay {
  position: relative;
}
.overlay::before {
  position: absolute;
  content: "";
  z-index: 3000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(18, 6, 0, 0.6);
  opacity: 0.8;
  will-change: display;
  transition: display 0.5s ease-out;
}

.red {
  color: var(--red_1);
}

h2 {
  font-family: "Inter SemiBold";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  font-size: 2.4rem;
  line-height: 120.02%;
  color: var(--black_1);
}
@media (min-width: 375px) {
  h2 {
    font-size: 2.6rem;
  }
}
@media (min-width: 1024px) {
  h2 {
    font-size: 4rem;
  }
}

.btn {
  text-align: center;
  padding: 1.25rem 2.885rem;
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  outline: none;
  border: none;
  border-radius: 5rem;
}
.btn--black {
  color: var(--white_1);
  background: var(--black_1);
}
.btn--red {
  color: var(--white_1);
  background: var(--red_1);
}
.btn--white {
  color: var(--black_1);
  border: 0.1rem solid var(--black_1);
  background: var(--white_1);
}

input,
textarea {
  width: 100%;
  padding: 1.55rem 2.4rem 1.55rem 2rem;
  margin-bottom: 2rem;
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-transform: uppercase;
  color: var(--gray_4);
  border: 0.1rem solid var(--white_3);
  border-radius: 0.5rem;
}
@media (min-width: 1024px) {
  input,
  textarea {
    padding: 2.1rem 2.4rem 2.1rem 2rem;
  }
}
input::placeholder,
textarea::placeholder {
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-transform: uppercase;
  color: var(--gray_4);
}

.upload {
  align-self: flex-start;
  margin-bottom: 2rem;
}

.upload .btn-tertiary {
  position: relative;
  font-family: "Inter Medium";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  color: var(--black_2);
  font-size: 1.4rem;
  line-height: 1.9rem;
  width: 20.8rem;
  text-align: left;
  display: block;
  border: 0.1rem solid var(--red_1);
}
@media (min-width: 1024px) {
  .upload .btn-tertiary {
    font-size: 1.6rem;
  }
}
.upload .btn-tertiary img {
  position: absolute;
  right: 0.5rem;
  top: 50%;
  transform: translateY(-50%);
}
@media (min-width: 1024px) {
  .upload .btn-tertiary img {
    right: 2rem;
  }
}

.upload .input-file {
  width: 0.1rem;
  height: 0.1rem;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.upload .input-file + .js-labelFile {
  width: 16rem;
  padding: 1.45rem 1rem;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .upload .input-file + .js-labelFile {
    width: auto;
    padding: 1.9rem 5.15rem 2rem 1.5rem;
  }
}

